import _ from "lodash";
import moment from "moment";
import React from "react";
import { Table } from "react-bootstrap";
import "./SmartTradeDetailsTable.css";

const SmartTradeDetailsTable = ({ smartTradeId, trades }: any) => {
  const triggerSignMap: any = {
    less: "<",
    less_or_equal: "<=",
    greater_or_equal: ">=",
    greater: ">",
  };

  return (
    trades && (
      <Table striped>
        <thead>
          <tr>
            <th>Order</th>
            <th>Price</th>
            <th>Amount</th>
            <th>Total</th>
            <th>Trigger</th>
            <th>Trailing</th>
            <th>Status</th>
            <th>Created</th>
            <th>Updated</th>
          </tr>
        </thead>
        <tbody>
          {trades.map((trade: any) => (
            <tr style={{ fontSize: "14px" }} key={trade.id}>
              <td>{`${trade.order_type} ${trade.order_side}`}</td>
              <td>
                <div>{`Desired: ${
                  trade.initial_price ? trade.initial_price : ""
                }`}</div>
                <div>{`Real: ${
                  trade.realised_price ? trade.realised_price : ""
                }`}</div>
              </td>
              <td>
                <div>{`Desired: ${
                  trade.initial_amount ? trade.initial_amount : ""
                }`}</div>
                <div>{`Real: ${
                  trade.realised_amount ? trade.realised_amount : ""
                }`}</div>
              </td>
              <td>
                <div>{`Desired: ${
                  trade.initial_total ? trade.initial_total : ""
                }`}</div>
                <div>{`Real: ${
                  trade.realised_total ? trade.realised_total : ""
                }`}</div>
              </td>
              <td>
                <div>{`${
                  trade.trigger_condition
                    ? triggerSignMap[trade.trigger_condition]
                    : ""
                } ${trade.trigger_price ? trade.trigger_price : ""}`}</div>
              </td>
              <td>
                <div>{`${trade.trail_value ? trade.trail_value : ""} ${
                  trade.trail_percentage ? `(${trade.trail_percentage}%)` : ""
                }`}</div>
              </td>
              <td>
                <div>{`${trade.status ? trade.status : ""}`}</div>
              </td>
              <td>
                <div>{`${
                  trade.updated_at
                    ? moment(trade.created_at).format("DD/MM/YYYY")
                    : ""
                }`}</div>
                <div>{`${
                  trade.updated_at
                    ? moment(trade.created_at).format("HH:mm")
                    : ""
                }`}</div>
              </td>
              <td>
                <div>{`${
                  trade.updated_at
                    ? moment(trade.updated_at).format("DD/MM/YYYY")
                    : ""
                }`}</div>
                <div>{`${
                  trade.updated_at
                    ? moment(trade.updated_at).format("HH:mm")
                    : ""
                }`}</div>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    )
  );
};

export default SmartTradeDetailsTable;
