import moment from "moment";
import React, { useEffect, useRef } from "react";

export const usePrevious = (value: any) => {
  const ref = useRef();
  useEffect(() => {
    ref.current = value;
  });
  return ref.current;
};

export const convertFilterValueToDate = (filterFrom: any) => {
  const dateFormat = "YYYY-MM-DD";

  const filterValue = filterFrom.label;

  if (filterValue === "today") {
    return moment().subtract(1, "day").format(dateFormat);
  } else if (filterValue === "week") {
    return moment().subtract(1, "week").format(dateFormat);
  } else if (filterValue === "month") {
    return moment().subtract(1, "months").format(dateFormat);
  } else if (filterValue === "custom") {
    return filterFrom.from
      ? moment(filterFrom.from).format(dateFormat)
      : moment().format(dateFormat);
  }
  return "";
};
